(function() {
  'use strict';

  angular
    .module('helloAngular', ['ngRoute']);

})();

(function() {
  'use_strict';

  angular
    .module('helloAngular')
    .factory('ergastAPI', ergastAPI);

  /** @ngInject */
  function ergastAPI($http) {
    var ergastAPI = {};

    ergastAPI.getDrivers = function() {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2013/driverStandings.json?callback=JSON_CALLBACK'
      });
    }

    ergastAPI.getDriverDetails = function(id) {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2013/drivers/'+ id +'/driverStandings.json?callback=JSON_CALLBACK'
      });
    }

    ergastAPI.getDriverRaces = function(id) {
      return $http({
        method: 'JSONP',
        url: 'http://ergast.com/api/f1/2013/drivers/'+ id +'/results.json?callback=JSON_CALLBACK'
      });
    }

    return ergastAPI;
  }
})();

(function() {
  'use strict';

  angular
    .module('helloAngular')
    .controller('DriversController', DriversController);

  /** @ngInject */
  function DriversController(ergastAPI) {
    var vm = this;

    vm.nameFilter = '';
    vm.driversList = [];

    vm.searchFilter = function (driver) {
      var keyword = new RegExp(vm.nameFilter, 'i');
      return !vm.nameFilter || keyword.test(driver.Driver.givenName) || keyword.test(driver.Driver.familyName);
    };

    ergastAPI.getDrivers().success(function (response) {
      //Dig into the responde to get the relevant data
      vm.driversList = response.MRData.StandingsTable.StandingsLists[0].DriverStandings;
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('helloAngular')
    .controller('DriverController', DriverController);

  /** @ngInject */
  function DriverController($routeParams, ergastAPI) {
    var vm = this;

    vm.id = $routeParams.id;
    vm.races = [];
    vm.driver = null;

    ergastAPI.getDriverDetails(vm.id).success(function (response) {
        vm.driver = response.MRData.StandingsTable.StandingsLists[0].DriverStandings[0];
    });

    ergastAPI.getDriverRaces(vm.id).success(function (response) {
        vm.races = response.MRData.RaceTable.Races;
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('helloAngular')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('helloAngular')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/drivers/drivers.html',
        controller: 'DriversController',
        controllerAs: 'drivers'
      })
      .when('/drivers/:id', {
        templateUrl: 'app/driver/driver.html',
        controller: 'DriverController',
        controllerAs: 'driver'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

(function() {
  'use strict';

  angular
    .module('helloAngular');

})();

(function() {
  'use strict';

  angular
    .module('helloAngular')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();

angular.module("helloAngular").run(["$templateCache", function($templateCache) {$templateCache.put("app/driver/driver.html","<div class=container><a href=./#/drivers><i class=\"tiny material-icons\">arrow_back</i> Back to drivers list</a><div id=secondary class=main-nav><div><h3><img ng-src=assets/images/helmet.svg width=4% style=vertical-align:top> {{driver.driver.Driver.givenName}} {{driver.driver.Driver.familyName}}</h3></div><div class=driver-status><b>Country</b>: {{driver.driver.Driver.nationality}}<br><b>Team</b>: {{driver.driver.Constructors[0].name}}<br><b>Birth</b>: {{driver.driver.Driver.dateOfBirth}}<br><a href={{driver.driver.Driver.url}} target=_blank>Biography <i class=\"tiny material-icons\">open_in_new</i></a></div></div><div><h5>Formula 1 2013 Results</h5><table class=striped><thead><tr><th>Round</th><th>Grand Prix</th><th>Team</th><th>Grid</th><th>Race</th></tr></thead><tbody><tr ng-repeat=\"race in driver.races\"><td>{{race.round}}</td><td>{{race.raceName}}</td><td>{{race.Results[0].Constructor.name}}</td><td>{{race.Results[0].grid}}</td><td>{{race.Results[0].position}}</td></tr></tbody></table></div></div>");
$templateCache.put("app/drivers/drivers.html","<div class=container><input type=text ng-model=drivers.nameFilter placeholder=Search...><table><thead><tr><th colspan=4>Drivers Championship Standings</th></tr></thead><tbody><tr ng-repeat=\"driver in drivers.driversList | filter: drivers.searchFilter\"><td>{{$index + 1}}</td><td><a href=#/drivers/{{driver.Driver.driverId}}>{{driver.Driver.givenName}}&nbsp;{{driver.Driver.familyName}}</a></td><td>{{driver.Constructors[0].name}}</td><td>{{driver.points}}</td></tr></tbody></table></div>");}]);